<template>
	<div class="pc-login-main">
	</div>
</template>
<script>
    export default {
        data() {
            return {};
        },
        computed: {},
        created() {
            this.pcLogin();
        },
        methods: {
            pcLogin() {
                let code = this.$route.query.gl_code;
                let token = this.$store.getters.getUserToken;
                if (code) {
                    let toast1 = this.$toast.loading({
                        mask: true,
                        message: '',
                        duration: 0
                    });
                    this.$post('wx_write_pc_token', {user_token: token, code: code})
                        .then((msg) => {
                            toast1.clear();
                            if (msg) {
                                this.loginSuccess();
                            } else {
                                this.loginError();
                            }

                        })
                } else {
                    this.loginError();
                }


            },
            /*URL*/
            GetQueryString(name) {
                let reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
                let r = window.location.search.substr(1).match(reg);
                if (r != null) return unescape(r[2]);
                return null;
            }
            , loginSuccess() {
                this.$dialog.alert({
                    title: '',
                    message: ''
                }).then(() => {
                    this.$router.push('/');
                })
            }
            , loginError() {
                this.$dialog.alert({
                    title: '',
                    message: ''
                }).then(() => {
                    this.$router.push('/');
                })
            }
        },
    };
</script>
