<template>
	<div class="main">
		<div class="order-operation-body">
			<van-button round v-if="order_info.order_state === 1" type="default" @click="callOrder"></van-button>
			<van-button round v-if="order_info.order_state === 0" type="default" @click="delOrder"></van-button>
			<van-button round v-if="order_info.order_state === 1" type="danger" @click="goPay"></van-button>
			<van-button round v-if="order_info.order_state === 3" type="default" @click="takeOrder"></van-button>
			<!--			<van-button round v-if="order_info.order_state === 4 && order_info.evaluate_is === 0" type="default"></van-button>-->
			<van-button round v-if="order_info.order_state === 6" type="default" @click="callService"></van-button>
			<van-button round v-if="order_info.order_state === 4 ||order_info.order_state === 2" type="default"
				@click="$router.push('/afterSale/'+order_info.order_sn)">
			</van-button>
		</div>
	</div>
</template>
<script>
    export default {
        data() {
            return {};
        },
        props: ["order_info"],
        computed: {},
        created() {

        },
        methods: {
            /**/
            goPay() {
                let success_url = encodeURIComponent(this.$store.state.local_url + "#/home");
                let back_url = encodeURIComponent(window.location.href);
                switch (this.order_info.son_into_type) {
                    case 'pc':
                        this.$toast(',');
                        break;
                    case 'wx':
                        window.location.href = this.$store.state.api_url + "/api/v1/payment/user_order_payment?success_url=" + success_url
                            + "&back_url=" + back_url
                            + "&order_sn=" + this.order_info.order_sn
                            + "&user_token=" + this.$store.getters.getUserToken;
                        break;
                }

            },
            /**/
            callService() {
                let toast1 = this.$toast.loading({
                    mask: true,
                    message: '',
                    duration: 0
                });
                this.$post('user_call_after_sale', {
                    user_token: this.$store.getters.getUserToken,
                    order_sn: this.order_info.order_sn
                })
                    .then((msg) => {
                        toast1.clear();
                        if (msg) {
                            this.$store.dispatch("getOrderList", this.$store.getters.getUserToken);
                            this.$router.push('/home');
                        } else {
                            this.$toast('')
                        }

                    })
            },
            /**/
            callOrder() {
                let toast1 = this.$toast.loading({
                    mask: true,
                    message: '',
                    duration: 0
                });
                this.$post('user_call_order', {
                    user_token: this.$store.getters.getUserToken,
                    order_sn: this.order_info.order_sn
                })
                    .then((msg) => {
                        toast1.clear();
                        if (msg) {
                            this.$store.dispatch("getOrderList", this.$store.getters.getUserToken);
                            this.$router.push('/home');
                        } else {
                            this.$toast('')
                        }

                    })
            },
            /**/
            delOrder() {
                let toast1 = this.$toast.loading({
                    mask: true,
                    message: '',
                    duration: 0
                });
                this.$post('user_del_order', {
                    user_token: this.$store.getters.getUserToken,
                    order_sn: this.order_info.order_sn
                })
                    .then((msg) => {
                        toast1.clear();
                        if (msg) {
                            this.$store.dispatch("getOrderList", this.$store.getters.getUserToken);
                            this.$router.push('/home');
                        } else {
                            this.$toast('')
                        }

                    })
            },
            /**/
            takeOrder() {
                let toast1 = this.$toast.loading({
                    mask: true,
                    message: '',
                    duration: 0
                });
                this.$post('user_take_order', {
                    user_token: this.$store.getters.getUserToken,
                    order_sn: this.order_info.order_sn
                })
                    .then((msg) => {
                        toast1.clear();
                        if (msg) {
                            this.$store.dispatch("getOrderList", this.$store.getters.getUserToken);
                            this.$router.push('/home');
                        } else {
                            this.$toast('')
                        }

                    })
            }
        },
    };
</script>
<style lang="scss" scoped>
	.main {
		.d {
			margin-top: 20px;
			width: 100%;
			height: 50px;
		}

		.order-operation-body {
			width: 100%;
			height: 50px;
			position: fixed;
			bottom: 0;
			background-color: white;
			box-shadow: 0px 0px 1px 0px rgba(0, 0, 0, 0.1);
			display: flex;
			justify-content: flex-end;
			align-items: center;

			button {
				display: block;
				height: 80%;
				margin-left: 5px;
			}
		}
	}
</style>