<template>
	<div class="main">
		<myNarBar title=""></myNarBar>
		<van-address-edit
			:area-list="areaList"
			:address-info="address_info"
			:show-delete="!this.address_edit.is_add"
			@save="onSave"
			@delete="onDelete"
		/>
	</div>

</template>

<script>
    import myNarBar from '../../sub/my-nav-bar';
    import areaList from './area'

    export default {
        data() {
            return {
                areaList,//
                address_edit: {},
            };
        },
        computed: {
            address_info: {
                get: function () {
                    var result = {};
                    if (!this.address_edit.is_add) {
                        result.id = this.address_edit.address_info.address_id;//id
                        result.name = this.address_edit.address_info.name;//
                        result.tel = this.address_edit.address_info.tel;//
                        result.province = this.address_edit.address_info.province;//
                        result.city = this.address_edit.address_info.city;//
                        result.county = this.address_edit.address_info.county;//
                        result.areaCode = this.address_edit.address_info.area_code;//
                        result.addressDetail = this.address_edit.address_info.address_detail;//
                    }
                    return result;
                }
            }
        },
        created() {
            this.address_edit = JSON.parse(this.$route.query.address_edit);
        },
        methods: {
            AddressEditShow() {
                this.$emit('AddressEditShow', false);
            }
            , onSave(content) {
                if (this.address_edit.is_add) {
                    //
                    let toast1 = this.$toast.loading({
                        mask: true,
                        message: '',
                        duration: 0
                    });
                    let post_info = {
                        user_token: this.$store.getters.getUserToken,
                        name: content.name,
                        tel: content.tel,
                        province: content.province,
                        city: content.city,
                        county: content.county,
                        address_detail: content.addressDetail,
                        area_code: content.areaCode
                    };
                    this.$post('user_add_address', post_info)
                        .then((msg) => {
                            //id
                            if (msg) {
                                toast1.clear();
                                this.$router.go(-1);
                            }
                        })
                } else {
                    //
                    let toast1 = this.$toast.loading({
                        mask: true,
                        message: '',
                        duration: 0
                    });
                    this.$post('user_upd_address', {
                        user_token: this.$store.getters.getUserToken,
                        address_id: content.id,
                        name: content.name,
                        tel: content.tel,
                        province: content.province,
                        city: content.city,
                        county: content.county,
                        address_detail: content.addressDetail,
                        area_code: content.areaCode
                    })
                        .then(() => {
                            toast1.clear();
                            this.$router.go(-1);
                        })

                }

            }
            , onDelete(content) {
                let toast1 = this.$toast.loading({
                    mask: true,
                    message: '',
                    duration: 0
                });
                this.$post('user_del_address', {
                    user_token: this.$store.getters.getUserToken,
                    address_id: content.id
                })
                    .then(() => {
                        toast1.clear();
                        this.$router.go(-1);
                    })
            }
        },
        components: {
            myNarBar,//
        },
    };
</script>

<style lang="scss">
	.van-picker__columns {
		.van-picker__frame {
			position: absolute !important;
		}
	}

</style>