import Vue from 'vue'
import Router from 'vue-router'
import VueCookies from 'vue-cookies'

Vue.use(Router);


// 
import Index from './components/tabber/Index.vue'
import Login from './components/Login'
import First from './components/tabber/first/First'//
import AboutMall from './components/tabber/aboutMall/AboutMall'//
import Classify from './components/tabber/classify/Classify'//
import Spike from './components/tabber/spike/Spike'//
import Cart from './components/tabber/cart/Cart'//
import Home from './components/tabber/home/Home'//
import AllCoupon from './components/coupon/AllCoupon' //
import Goods from './components/goods/Index'//
import supplier from './components/supplier/Supplier'//
import myIframe from './components/sub/my-iframe'//
import writeOrder from './components/order-form/write-order'//
import addressList from './components/address/address'//
import couponList from './components/coupon/coupon'//
import AddressEdit from './components/address/sub/my-address-edit'//
import SeeOrder from './components/order-form/see-order'//
import orderList from './components/tabber/home/order_list/order_list'//
import goodsList from './components/goods-list/goods-list'//
import evaluateList from './components/tabber/home/evaluate-list/evaluate-list'//
import putEvaluate from './components/tabber/home/evaluate-list/put-evaluate/put-evaluate'//
import Search from './components/search/my-search'//
import afterSale from './components/tabber/home/after-sale/after-sale' //
import updateUserInfo from './components/tabber/home/update-user-info/update-user-info' //
import balance from './components/balance/balance' //
import balanceList from './components/balance/balance-list/balance-list' //
import Article from './components/article/article' //
import PcLogin from './components/PcLogin/PcLogin' //pc
import Activity01 from './components/activity/activity01/index'
import ActivityStationery from "./components/activity/activity_stationery/ActivityStationery"; //
import ActivityByPage from "./components/activity/ActivityByPage";//
import ActivityPromptGoodsPage from "./components/activity/ActivityPromptGoodsPage";//
import ActivityRecommendPage from "./components/activity/ActivityRecommendPage";


// 3. 
var router = new Router({
    routes: [
        // 
        {path: '/', redirect: '/index'},
        {
            path: '/index', component: Index,
            meta: {
                keepAlive: true, //
            }, children: [
                {path: '/', redirect: '/first', meta: {keepAlive: true}},
                {path: '/first', component: First, meta: {keepAlive: true}},
                {path: '/aboutMall', component: AboutMall, meta: {keepAlive: true}},
                {path: '/classify', component: Classify, meta: {keepAlive: true}},
                {path: '/spike', component: Spike, meta: {keepAlive: true}},
                {path: '/cart', component: Cart, meta: {keepAlive: false}},
                {path: '/home', component: Home, meta: {keepAlive: true}}
            ]
        },
        {path: '/login', component: Login},
        {path: '/goods/:goods_id', component: Goods},
        {path: '/allCoupon/', component: AllCoupon},
        {path: '/supplier', component: supplier, meta: {keepAlive: true}},
        {path: '/article/:article_id', component: Article},
        {path: '/myIframe', component: myIframe},
        {path: '/writeOrder', component: writeOrder},
        {path: '/seeOrder/:order_sn', component: SeeOrder},
        {path: '/addressList', component: addressList},
        {path: '/couponList', component: couponList},
        {path: '/addressEdit', component: AddressEdit},
        {path: '/orderList/:type_name', component: orderList},
        {path: '/evaluateList', component: evaluateList},
        {path: '/putEvaluate', component: putEvaluate},
        {path: '/search', component: Search},
        {path: '/balance', component: balance},
        {path: '/balanceList/:bystages_code', component: balanceList},
        {path: '/updateUserInfo', component: updateUserInfo},
        {path: '/afterSale/:order_sn', component: afterSale},
        {path: '/goodsList', component: goodsList, meta: {keepAlive: true}},
        {path: '/pcLogin', component: PcLogin},
        {path: '/activity01', component: Activity01, meta: {keepAlive: true}},
        {path: '/activity_stationery', component: ActivityStationery, meta: {keepAlive: true}},
        {path: '/activity_by', component: ActivityByPage, meta: {keepAlive: true}},
        {path: '/activity_prompt_goods', component: ActivityPromptGoodsPage, meta: {keepAlive: true}},
        {path: '/activity_recommend', component: ActivityRecommendPage, meta: {keepAlive: true}},
    ],
    scrollBehavior(to, from, savedPosition) {
        if (savedPosition) {
            return savedPosition
        } else {
            return {x: 0, y: 0}
        }
        // return 
    }
});

router.beforeEach((to, from, next) => {
    localStorage.setItem('beforeUrl', from.path);// 
    const userTokenName = process.env.VUE_APP_USER_TOKEN_NAME;
    let user_token = VueCookies.get(userTokenName);
    if (user_token === null && to.path !== '/login') {
        // 
        localStorage.setItem('beforeLoginUrl', to.fullPath);// url
        next('/login');
        return false
    }
    next()
});


// 
export default router